<template>
  <div class="context">
    <div class="content">
      <div class="table-container">
        <table id="grid-basic" class="filterable-table bootgrid-table">
          <thead>
          <tr>
            <th data-column-id="id" data-type="numeric">ID</th>
            <th data-column-id="email">Логин</th>
            <th data-column-id="roles">Группы</th>
            <th data-column-id="change-password">Пароль</th>
          </tr>
          </thead>
          <tbody id="grid-basic-result">
          <tr v-for="user in users">
            <td>{{ user.id }}</td>
            <td>{{ user.email }}</td>
            <td v-on:click="editUserRoles(user)">{{ user.roles.reduce((acc, cur) => acc + ", " + cur.name, '').substr(1) }}</td>
            <td v-on:click="generateNewPassword(user)" :id='"password-" + user.id'><i class="fas fa-edit"></i></td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import '@/external/jquery.dataTables.min.css'
import '@/external/jquery.dataTables.min.js'
import Connection from "@/rsocket/connection";
import Vue from "vue";
import Swal from "sweetalert2";
import notification from "@/notify/notification";
import router from "@/views/router/routes";

export default {
  name: "UserList",
  methods: {
    generateNewPassword(user) {
      Swal.fire({
        title: 'Вы уверены?',
        text: "Данное действие сгенерирует пользователю новый пароль!",
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: 'Да! Я знаю, что делаю.',
        cancelButtonText: 'Нет! Я еще подумаю.'
      }).then((result) => {
        if (result.isConfirmed) {
          Connection.generateNewPassword(user.email, (res) => {
            $('#password-' + user.id).html(JSON.parse(res.data.toString()).password).css("pointer-events","none");
          }, (error) => {
            notification('error', "Не удалось сгенерировать новый пароль");
          });
        }
      });
    },
    editUserRoles(user) {
      router.push("/dashboard/talkytime/user-roles/" + user.id);
    }
  },
  mounted() {
    Connection.userList((res) => {
      let data = JSON.parse(res.data.toString());
      Vue.$log.debug(data);
      this.users = data.users;
    }).then(() => {
      $('#grid-basic').DataTable();
    });
  },
  data: () => {
    return {
      users: []
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  box-sizing: border-box;
  background: #222427;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-align: center;
}

.table-container {
  background: #2c2f33;
  color: #fff;
}

.filterable-table {
  overflow: hidden;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  text-align: center;
  background-color: #494e55;
  font-weight: bold;

  tbody tr {
    border-bottom: 2px solid #0000006c;
  }
}

@media screen and (max-width: 768px) {
  table.filterable-table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }

  table.filterable-table tr {
    border-bottom: 1px solid #ddd;
    display: block;
    margin-bottom: 30px;
  }

  table.filterable-table td {
    display: block;
    text-align: right;
  }

  table.filterable-table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }

  table.filterable-table td:last-child {
    border-bottom: 0;
  }
}
</style>